___CSS_LOADER_EXPORT___.push([module.id, `* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  background-color: rgb(224, 215, 203);
}

.wrapper-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 3px solid rgb(224, 215, 203);
  background-color: whitesmoke;
  margin: 10% 20% 0;
  border-radius: 12px;
}

.big-title {
  align-self: center;
  margin-left: 5rem;
  padding-top: 30px;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 30px;
  width: 90%;
  margin: 22px 0;
}

.recent-content {
  background-color: lightgreen;
  width: 100%;
  padding: 10px 20px;
  border-radius: 8px;
}

.score {
  display: flex;
  justify-content: space-between;
  margin-left: 12px;
  border-bottom: 2px solid whitesmoke;
  font-size: 25px;
  font-weight: 700;
  line-height: 24px;
  padding: 10px;
}

.btn {
  border: none;
  background-color: transparent;
  cursor: pointer;
  font-style: italic;
}

.score-board {
  padding: 12px;
  font-size: 16px;
  line-height: 17px;
  font-weight: 300;
}

.score-board li:nth-child(even) {
  background-color: whitesmoke;
}

.score-board li:nth-child(odd) {
  background-color: lightgreen;
}

.score-board-content {
  border-bottom: 1px solid whitesmoke;
  padding: 8px;
  list-style-type: none;
}

form {
  display: flex;
  flex-direction: column;
  padding: 10px;
  border-radius: 6px;
  gap: 12px;
  width: 100%;
}

form input {
  padding: 10px;
  border-radius: 6px;
}

.submit {
  align-self: flex-end;
  padding: 4px;
  width: 30%;
  border-radius: 6px;
  cursor: pointer;
  background-color: lightgreen;
}

.submit:hover {
  background-color: green;
}

.submit:active {
  background-color: darkgreen;
}`, "",{"version":3,"sources":["webpack://./src/styles/style.css"],"names":[],"mappings":"AAAA;EACI,UAAA;EACA,SAAA;EACA,sBAAA;AACJ;;AAEE;EACE,oCAAA;AACJ;;AAEE;EACE,aAAA;EACA,sBAAA;EACA,uBAAA;EACA,mBAAA;EACA,oCAAA;EACA,4BAAA;EACA,iBAAA;EACA,mBAAA;AACJ;;AAEE;EACE,kBAAA;EACA,iBAAA;EACA,iBAAA;AACJ;;AAEE;EACE,aAAA;EACA,uBAAA;EACA,mBAAA;EACA,SAAA;EACA,UAAA;EACA,cAAA;AACJ;;AAEE;EACE,4BAAA;EACA,WAAA;EACA,kBAAA;EACA,kBAAA;AACJ;;AAEE;EACE,aAAA;EACA,8BAAA;EACA,iBAAA;EACA,mCAAA;EACA,eAAA;EACA,gBAAA;EACA,iBAAA;EACA,aAAA;AACJ;;AAEE;EACE,YAAA;EACA,6BAAA;EACA,eAAA;EACA,kBAAA;AACJ;;AAEE;EACE,aAAA;EACA,eAAA;EACA,iBAAA;EACA,gBAAA;AACJ;;AAEE;EACE,4BAAA;AACJ;;AAEE;EACE,4BAAA;AACJ;;AAEE;EACE,mCAAA;EACA,YAAA;EACA,qBAAA;AACJ;;AAEE;EACE,aAAA;EACA,sBAAA;EACA,aAAA;EACA,kBAAA;EACA,SAAA;EACA,WAAA;AACJ;;AAEE;EACE,aAAA;EACA,kBAAA;AACJ;;AAEE;EACE,oBAAA;EACA,YAAA;EACA,UAAA;EACA,kBAAA;EACA,eAAA;EACA,4BAAA;AACJ;;AAEE;EACE,uBAAA;AACJ;;AAEE;EACE,2BAAA;AACJ","sourcesContent":["* {\r\n    padding: 0;\r\n    margin: 0;\r\n    box-sizing: border-box;\r\n  }\r\n  \r\n  body {\r\n    background-color: rgb(224, 215, 203);\r\n  }\r\n  \r\n  .wrapper-content {\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: center;\r\n    align-items: center;\r\n    border: 3px solid rgb(224, 215, 203);\r\n    background-color: whitesmoke;\r\n    margin: 10% 20% 0;\r\n    border-radius: 12px;\r\n  }\r\n   \r\n  .big-title {\r\n    align-self: center;\r\n    margin-left: 5rem;\r\n    padding-top: 30px;\r\n  }\r\n  \r\n  .container {\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    gap: 30px;\r\n    width: 90%;\r\n    margin: 22px 0;\r\n  }\r\n  \r\n  .recent-content {\r\n    background-color: lightgreen;\r\n    width: 100%;\r\n    padding: 10px 20px;\r\n    border-radius: 8px;\r\n  }\r\n  \r\n  .score {\r\n    display: flex;\r\n    justify-content: space-between;\r\n    margin-left: 12px;\r\n    border-bottom: 2px solid whitesmoke;\r\n    font-size: 25px;\r\n  